.PortfolioSub {
	.inner {
		&::after {
			content: '';
			display: none;
			clear: both;
		}
		#searchBox {
			float: left;
			width: 100%;
			display: flex;
			justify-content: center;
			overflow: hidden;
			margin-bottom: 50px;
			font-size: 0;
			input[type='text'] {
				width: 270px;
				height: 30px;
				background-color: #0b0909;
				border: none;
				border-bottom: 1px solid #6e6d6d;
				padding-left: 10px;
				&:focus {
					outline: none;
				}
			}
			.btnSearch {
				width: 80px;
				height: 30px;
				background-color: #cb770a;
				font-size: 12px;
				line-height: 30px;
				text-align: center;
				border: none;
				cursor: pointer;
				margin-left: 20px;
				vertical-align: top;
				transition: 0.5s;
				&:hover {
					background-color: #eb7206;
				}
			}
		}
		.btns {
			width: 200px;
			float: left;
			display: flex;
			flex-wrap: wrap;
			gap: 25px;
			margin-top: 10px;
			button {
				width: 100%;
				text-align: left;
				border: none;
				background: transparent;
				font-size: 14px;
				color: #9a9a9a;
				cursor: pointer;
				position: relative;
				transition: 0.5s;
				&:hover {
					color: #cb770ab3;
				}
				&.on {
					color: #ea761e;
				}
			}
		}
		.loading {
			width: 200px;
			position: absolute;
			top: 450px;
			left: 50%;
			transform: translateX(-50%);
		}
		#gallery {
			float: right;
			width: 980px;
			padding: 0px 0px 50px;
			overflow: hidden;
			&.on .list {
				opacity: 1;
				transform: translateY(0px);
			}
			.list {
				opacity: 0;
				transform: translateY(100px);
				transition: 1s;
				&::after {
					content: '';
					display: block;
					clear: both;
				}
				li {
					width: calc(100% / 4);
					float: left;
					padding: 10px;
					margin-bottom: 20px;
					div {
						width: 100%;
						border: 1px solid #6e6d6d80;
						padding: 20px 10px;
						transition: 0.5s;
						cursor: pointer;
						&:hover {
							border: none;
							background-color: #cb770ad8;
							p {
								color: #efefef;
								margin-top: 0px;
								&::before {
									display: none;
								}
							}
						}
						a {
							img {
								width: 100%;
							}
						}
						p {
							font-weight: bold;
							font-size: 13px;
							line-height: 1.2;
							text-align: center;
							color: #9a9a9a;
							margin-top: 20px;
							padding-top: 15px;
							position: relative;
							transition: 0.5s;
							&::before {
								content: '';
								display: block;
								width: calc(100% + 20px);
								height: 1px;
								background-color: #6e6d6d80;
								position: absolute;
								top: 0;
								left: -10px;
							}
						}
					}
				}
			}
		}
	}
}

// tablet - 1179px--------------------

@media screen and (max-width: $tablet) {
	.PortfolioSub {
		.inner {
			.btns {
				width: 18%;
			}
			#gallery {
				width: 80%;
			}
		}
	}
}

// middle - 935px--------------------

@media screen and (max-width: $middle) {
	.PortfolioSub {
		.inner {
			#gallery {
				.list {
					li {
						width: calc(100% / 3);
					}
				}
			}
		}
	}
}

// mobile - 539px -------------------
@media screen and (max-width: $mobile) {
	.PortfolioSub {
		.inner {
			.btns {
				button {
					font-size: 11px;
				}
			}
			#gallery {
				width: 70%;
				.list {
					li {
						width: calc(100% / 1);
					}
				}
			}
		}
	}
}
