header {
	width: 100%;
	padding: 50px 0vw 0vh;
	.inner {
		width: 1180px;
		height: 110px;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		position: relative;
		> h1 {
			width: 140px;
			margin-right: 160px;
			font-weight: 400;
			a {
				display: block;
				width: 100%;
				font-size: 16px;
				transition: 0.5s;
				&:hover {
					color: #cb770a;
				}
			}
		}
		.menuWeb {
			width: 75%;
			&::after {
				content: '';
				display: block;
				clear: both;
			}
			#gnb {
				float: left;
				li {
					float: left;
					margin-right: 20px;
					a {
						font-size: 14px;
						transition: 0.5s;
						&:hover {
							color: #cb770a;
						}
					}
				}
			}
			.sns {
				float: right;
				li {
					float: left;
					margin-right: 20px;
					&:last-of-type {
						margin-right: 0px;
					}
					a {
						font-size: 14px;
						svg {
							path {
								transition: 0.5s;
							}
							&:hover {
								path {
									color: #cb770a;
								}
							}
						}
					}
				}
			}
		}
		.btnCall {
			position: absolute;
			right: 0vw;
			top: 10%;
			transform: translateY(-50%);
			display: none;
			width: 20px;
			height: 20px;
			&::before {
				content: '';
				display: block;
				width: 100%;
				height: 2px;
				background-color: #efefef;
				position: absolute;
				top: 0;
				left: 0;
				transition: top 0.5s 0s, transform 0.5s 0.5s;
			}
			&::after {
				content: '';
				display: block;
				width: 100%;
				height: 2px;
				background-color: #efefef;
				position: absolute;
				bottom: 0;
				left: 0;
				transition: bottom 0.5s 0s, transform 0.5s 0.5s;
			}
			span {
				display: block;
				width: 100%;
				height: 2px;
				background-color: #efefef;
				position: absolute;
				top: 50%;
				left: 0;
				margin-top: -1px;
				transition: 1s;
				text-indent: -9999px;
			}
			&.on {
				&::before {
					top: 50%;
					margin-top: -1px;
					transform: rotate(45deg);
				}
				&::after {
					bottom: 50%;
					margin-bottom: -1px;
					transform: rotate(-45deg);
				}
				span {
					transform: translateX(100%);
					opacity: 0;
				}
			}
		}
	}
}
@media screen and (max-width: $tablet) {
	header {
		padding: 5vh 5vw 0vh;
		.inner {
			width: 100%;
			.menuWeb {
				display: none;
			}
			.btnCall {
				display: block;
			}
		}
		&.main {
			.inner {
				> h1 {
					opacity: 0;
					position: absolute;
					top: -999999px;
				}
			}
		}
		&.sub {
			.inner {
				> h1 {
					width: 100%;
				}
			}
		}
	}
}
